*,
::before,
::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: #f5f0a6;
}
.App {
  padding: 0px;
  display: flex;
  flex-direction: column;
}

.hamburger {
  position: fixed;
  top: 20px;
  right: 20px;
}

@media (min-width: 400px) {
  .text {
    width: 80%;
  }
}
@media (min-width: 600px) {
  .text {
    width: 50%;
  }
}
